/*
 * Content is top level container between Top Navigation menu and bottom footer
 * html/body
 *         |---------------------------|
 *         |      Navbar (Fixed)       |
 *         |---------------------------|
 *         |         Content           |
 *         |---------------------------|
 *         |      footer (Sticky)      |
 *         |---------------------------|
 * Content is a flex box in order to make child elements arrangement easier.
 * There are 3 children elements: 
 *    Featured Image    (.fi)
 *    Sidebar           (.sidebar)
 *    Article contents  (.article)
 * Layout 1 (Article page and Landing page on default desktop client):
 *         |---------------------------|
 *         |      Feature Image        |
 *         |---------------------------|
 *         | Sidebar |    Article      |
 *         | (Sticky)|                 |
 *         |---------------------------|
 *
 * Layout 2 (Non-Article page on default desktop client):
 *         |---------------------------|
 *         |      Feature Image        |
 *         |---------------------------|
 *         |         Article           |
 *         |---------------------------|
 *
 * Layout 3 (Mobile small screen):
 *         |---------------------------|
 *         |         Sidebar           |
 *         |---------------------------|
 *         |      Feature Image        |
 *         |---------------------------|
 *         |         Article           |
 *         |---------------------------|
*/

// Top level content Layout
.content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  //align-content: space-between;
  align-content: flex-start;
  justify-content: space-between;

  width: 100%;
  margin: 0 auto;        // Keep it in middle of screen width
  padding: 0 1em;
  max-width: $max-width-content;
  min-height: calc( 100vh - var(--header-height) * 2 );

  .fi {
    order: 2;
    width: 100%;
    height: 25vh;
    margin-right: auto;
  }

  .sidebar {
    order: 3;           // switch to 1 for mobile
    width: 15em;
    @media only screen and (max-width: $m-break) {
      order: 1;         // Jump in front of Featured image
      width: 100%;
      //min-height: 0;
      .bottom {
        position: relative;
      }
    }
  }

  .article {
    order: 4;
    //flex-grow: 1;
    //flex-shrink: 1;
    //flex-basis: 100px;     // 450px to avoid article wrap below sidebar. change rem to 16 below $m-break
    width: 620px;
    @media only screen and (max-width: $m-break) {
      width: 100%;
    }
  }
}

.nosidebar {
  > .sidebar {
    display: none;
  }
  > .article {
    width: 100%
  }
}

/* --- Sidebar class --- */
.sidebar {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
  position: sticky;
  //position: fixed;
//  top: calc( var(--header-height) + 25vh);
  top: calc( var(--header-height) );

  /* Keep sidebar footer at bottom using vh unit */
  min-height: calc(100vh - 25vh - var(--header-height) * 2);

  padding: 0.5em 1em 1em 0em;
  background-color: #ffffff;

  // Fix share buttons at bottom of sidebar
  .bottom {
    position: fixed;
    bottom: var(--header-height);
  }
}

.sidebar-container {
  //color: #bfbfbf;
  //font-size: 14px;

