.contact-title {
  position: relative;
  color: #333030;
  font-weight: 400;
  //font-size: 12px;
  //text-transform: uppercase;
  text-align: center;
  width: 100%;

  p {
    margin: 0;
  }
  &::before {
    content: "";
    display: block;
    height: 2px;
    width: -webkit-calc(100%);
    width: 100%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    position: absolute;
    top: 2px;
    left: 0;
    background-color: #515151;
  }
  
  &::after {
    content: "";
    display: block;
    height: 2px;
    width: -webkit-calc(100%);
    width: calc(100%);
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    position: absolute;
    //bottom: 0px;
    right: 0;
    background-color: #515151;
  }
}

p.about {
