* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

figure {
  margin: 0;
}

// For correct line number width in Github Gists
.gist table tbody tr td {
  box-sizing: content-box;
}

pre {
  overflow-x: scroll;
}

html {
  background: $background-color;
  font-size: $font-size-root;     // Set 1rem === 20px
}

// Typography
::selection {
  background: $selection-color;
}

::-moz-selection {
  background: $selection-color;
}

body {
  color: $text-color;
  font-family: $font-family-main;
  font-size: 1rem;              // 20px by default
  line-height: 1.65em;          // 33px by default
  padding: 0;
  margin: 0;
  word-wrap: break-word;
  scroll-behavior: smooth;
}

.title,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $font-family-headings;

  a {
    color: $text-color;
  }

}

.title {
  font-size: 2.6rem;      // 52px
  line-height: 1.519em;   // 79px
  margin-bottom: 0.635em; // 33px
}

h1 {
  font-size: 2.05rem;     // 41px
  line-height: 1.537em;   // 63px
  margin-bottom: 0.805em; // 33px
}

h2 {
  font-size: 1.6rem;      // 32px
  line-height: 1.563em;   // 50px
  margin-top: 1.656em;    // 53px
  margin-bottom: 0.625em; // 20px
}

h3 {
  font-size: 1.25rem;     // 25px
  line-height: 1.6em;     // 40px
  margin-top: 1.32em;     // 33px
  margin-bottom: 0.48em;  // 12px
}

h4 {
  font-size: 1rem;        // 20px
  line-height: 1.65em;    // 33px
  font-weight: bold;
  margin-bottom: 0.35em;  // 7px
}

iframe,
img,
embed,
object,
video {
  max-width: 100%;
  padding: 0 0;
}

img[align=left] {
  margin-right: 3%;
}

img[align=right] {
  margin-left: 3%;
}

/* ---- Responsive ---- */

@media screen and (max-width: $break) {
  h1 {
    font-size: 2rem;
  }
  h2 {
    font-size: 1.5rem;
  }
  h3 {
    font-size: 1rem;
  }
  h4 {
    font-size: 0.9rem;
  }
  tr, th, .meta {
    font-size: 0.9em;
  }
  p, li, td {
    font-size: 0.8em;
  }
  li li {
    font-size: 0.95em;
  }
  li li * {
    font-size: 1em;
  }

  iframe,
  img,
  embed,
  object,
  video {
    max-width: 100%;
    padding: 0 1%;
  }
}

@media screen and (max-width: $sm-break) {
  h1 {
    font-size: 1.5rem;
  }
  h2 {
    font-size: 1rem;
  }
  h3 {
    font-size: 0.9rem;
  }
  h4 {
    font-size: 0.85rem;
  }
  th, tr, .meta {
    font-size: 0.85em;
  }
  p, li, td {
    font-size: 0.8em;
  }
  li li {
    font-size: 0.95em;
  }
  li li * {
    font-size: 1em;
  }

  table {
    overflow-x: scroll;
  }
}

blockquote {
  border-left: 2px solid darken($blockquote-color, 20%);
  margin: 1em 1em;
  padding: 0.75em 1em;
  background-color: $blockquote-color;
  box-shadow: 0 2px 2px $border-color;
}

blockquote *:last-child,
footer *:last-child {
  margin-bottom: 0;
}

table {
  table-layout: fixed;
  width: 100%;
  word-wrap: break-word;
  display: inline-block;
}

td,
th {
  padding: 0.5em 1em;
  border: 1px solid $border-color;
  text-align: left;
}

table,
dl,
kbd,
samp {
  margin: 1em 0;
}

dt {
  font-weight: bold;
}

dd {
  margin-left: 2em;
}

p,
ol,
ul,
dl,
.math-display {
  line-height: 1.5;
  margin-bottom: 1em;
}

// KaTeX math display
.math-display {
  display: inline-block;
  width: 100%;
}

// Lists within lists
li {
  p {
    display: inline;
  }

  & > ul,
  & > ol {
    margin-bottom: 0;
    margin-left: 1em;
  }
}

ol,
ul {
  list-style-position: outside;
  margin-left: 1em;
}

hr {
  border: 0;
  border-top: 1px solid $border-color;
  border-bottom: 1px solid #fff;
  margin: 1em 0;
}

a {
  color: $link-color;
  text-decoration: none;
}

kbd {
  background-color: #eee;
  border-radius: 3px;
  border: 1px solid #b4b4b4;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .2), 0 2px 0 0 rgba(255, 255, 255, .7) inset;
  color: #333;
  display: inline-block;
  font-size: .85em;
  font-weight: 700;
  line-height: 1;
  padding: 2px 4px;
  white-space: nowrap;
}
