/* Styles for archive pages: 1 or multiple lists
	Categories
	Tags
	Timeline
	archive pages created by jekyll-archives per category, tag, year/month/day
*/
.groups {
	display: flex;
	flex-wrap:wrap;
	flex-direction:row;
	justify-content:flex-start;
	align-items:stretch;
	font-size: .9em;

	.count {
		color: $meta;
		font-size: .7em;
		position: relative;
		top: -2px;
	}
	
	.group {
		flex: 0 0 100%;
	
		h3 {
			text-transform: capitalize;
			font-weight: 500;
			margin-bottom: 20px;
		}
	
		@media screen and (min-width: $break) {
			flex: 0 0 50%;
		}
	
		@media screen and (min-width: $m-break) {
			flex: 0 0 33%;
		}
		margin: 0 0 30px 0;
	
		ul {
			padding: 0;
			margin: 0;
			list-style: none;
		}
	
		li {
			margin-right: 15px;
			a {
				padding: 2px 0;
				display: flex;
				font-size: 1.1em;
				align-items: center;
	
				svg {
					margin: 0 5px 0 0;
					fill: $link-color;
					width: 25px;
					height: 25px;
					-webkit-transition: all .2s ease;
				}
	
				&:hover {
					text-decoration: none;
					svg {
						fill: $link-hover-color;
						width: 30px;
						height: 30px;
					}
				}
			}
		}
	}
}

.listing {
	list-style: none;
	padding: 0;
	margin: 0;
	display: flex;
	flex-flow: wrap;

	h4, h5 {
		margin: 0;
	}

	ul {
		padding: 0 0 0 20px;
	}

	li:last-child a:after {
		background: none;
		height: 0;
		width: 0;
	}

	li {
		flex: 0 0 100%;
		margin: 0;      // 10px 0;
		padding: 0;
		//@media screen and (min-width: $break) {
		//	flex: 0 0 50%;
		//}

		&.active a:before {
			color: $meta;
		}

		a {
			display: flex;
			position: relative;
			text-decoration: none;
			padding: 5px;
			border-radius: 5px;
			margin: 0 0 0 $padding-x-small;
			//height: 100%;
	
			&:before {
				content: "\25BA";
				position: absolute;
				display: block;
				top: 50%;
				-webkit-transform: translateY(-50%);
				-moz-transform: translateY(-50%);
				-ms-transform: translateY(-50%);
				transform: translateY(-50%);
				left: -23px;
				color: #e0e0e0;
				z-index: 1;
			}

			&:after {
				content: "";
				display: block;
				left: -22px;
				background: #e0e0e0;
				position: absolute;
				bottom: -43px;
				width: 2px;
				top: 39px;
			}

			article {
				display: inline-block;
				align-items: center;
				flex-wrap: wrap;
			}
	
			.meta {
				display: inline;
				float: right;
				font-size: 0.7em;
				line-height: normal;

				@media screen and (max-width: $sm-break) {
					display: none;
				}
			  }

			  .icon {
				flex: 0 0 50px;
				display: flex;
				align-items: center;
				padding: 0 5px 0 0;
				svg {
					width: 35px;
					height: 35px;
					fill: $link-color;
					-webkit-transition: all .2s ease;
					margin: 0 auto;
				}
			}
	
			.length {
				color: $meta;
				font-size: .8em;
			}
	
			&:hover {
				svg {
					fill: $link-hover-color;
					width: 40px;
					height: 40px;
				}
			}
	
			p {
				margin: 5px 0 0 0;
				color: #888;
				font-size: .9em;
				line-height: 1.4em;
				width: 100%;
			}
		}
	}
}
  
