/* --- Featured Image with different text overlay flavors --- */

// Provide 1 or more background images through style="background-image:, top 1st"
.fi {
  position: relative;
  //display: inline-block;
  overflow: hidden;
  border-radius: 15px;           // round image corners
  background-color: $theme-color;   // Shown when no -image provided
  background-position: center, center, center;  // positions for multiple images
  background-attachment: scroll;      // scroll | fixed | local
  background-repeat: no-repeat;    // repeat-x | repeat-y | [repeat | space | round | no-repeat]
  background-size: $feature-image-size;   // [ <length-percentage> | auto ] | cover | contain

  .meta {
      color: rgba($feature-image-text-color, 1);
  }

  /* --- Responsive --- */
  @media screen and (max-width: $break) {
    background-size: 200% auto;
  }

  @media screen and (max-width: $sm-break) {
    background-size: 400% auto;
  }
}

// Placeholder, no output from SASS preprocessor
.fi-placeholder {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}

// Complete overlay
.fi-ol {
  @extend .fi-placeholder;
}

// Top strap overlay
.fi-ol-top::before, .fi-ol-top::after {
  @extend .fi-placeholder;
  top: 0%;
  bottom: 66%;
}

// Middle strap overlay
.fi-ol-mid::before, .fi-ol-mid::after {
  @extend .fi-placeholder;
  top: 33.3%;
  bottom: 33.3%;
}

// Bottom strap overlay
.fi-ol-bottom::before, .fi-ol-bottom::after {
  @extend .fi-placeholder;
  top: 66%;
  bottom: 0%;
}

// Blur effect, before Pseudo element only to avoid conflict with darken effects
.fi-blur::before {
  content: "";
  background: inherit;
  -webkit-filter: blur(4px);
  filter: blur(4px);
}

// Darken effect, after Pseudo element only
.fi-darken::after {
  content: "";
  background-color: rgba(0,0,0,0.4)!important;
}

// Gradient darken effect, after Pseudo element only
.fi-gradient::after {
  content: "";
  background-color: linear-gradient(to up, rgba(0,0,0,0.6), rgba(0,0,0,0.4));
}

// Default text box will be centered in parent and size based on its content 
// 24cpl@620px line for 52px h1.title, 31cpl for h1@41px, 30cpl for h2@32px
// So avoid sub-title, just Title, 2 lines max (<48 characters)
.fi-tbox {
  @extend .fi-placeholder;
  padding: 5px 10px;
  z-index: 1;
  
  color: $feature-image-text-color;    // white is best
  //font-size: 1.2em;
  text-align: center;

  //For white images
  p, a, li, h1, h2, h3, h4, h5 {
    //font-family: $font-family-logo;
    @extend .header-txt-shadow;
    padding-top: 0;
    padding-bottom: 10px;
    margin: 5px 0;
    line-height: 1em;
    text-transform: capitalize;
  }

  p, a, li {
    text-align: left;
  }
  p:last-child {
    margin-bottom: 0;
  }

  h1.title {

  }
}

// text box at top strap
.fi-tbox-top {
  @extend .fi-tbox;
  top: 0%;
  bottom: 66%;
}

// text box at middle strap
.fi-tbox-mid {
  @extend .fi-tbox;
  top: 33.3%;
  bottom: 33.3%;
}

// text box at bottom strap
.fi-tbox-bottom {
  @extend .fi-tbox;
  top: 66%;
  bottom: 0%;
}

