.footer-wrapper {
  display: inline-block;
  text-align: center;
  width: 100%;
  height: var(--header-height);
  border-top: 1px solid #e7e7e7;
  color: lighten($text-color, 24%);
  font-size: 0.9em;

  .footer-icons {
    ul {
      list-style: none;
      margin: 0;

      li {
        display: inline;
      }

      a:hover {
        color: darken($link-color, 15%);
      }
    }
  }
}

.site-footer {
  display: block;
  text-align: center;
  width: 100%;
  height: 100%;
  max-width: $max-width-nav;
  margin: 0 auto;

  .text {
    text-align: center;
  }
}

footer .copyright {
  font-size: 14px;
  text-align: center;
  margin: 0;
}
