/* Desktop first responsive design, set default element properties for biggest screen */
// Be careful when using SCSS variables in CSS custom property, must use #{} enclosure
:root {
    /* This custom property affects sidebar position as well, hence in :root */
    --header-height: 3em;
}

.site-header {
  /* Define some local custom properties to avoid mistakes */
  --nav-height: calc(var(--header-height) * 2 / 3);    // derive menu content height from header area height
  --hide: none;                     // Display value to hide the flex container
  --flex-container-show: flex;      // Display value to show the flex container
  --flex-item-show: block;          // Display value to show the non-flex-container flex item
  --ul-show: block;                 // Display value to show ul if not flex box
  --dropdown-menu-width: 9em;       // Default dropdown menu width
  --menu-padding-left: 1em;         // Left padding for dropdown indicator and nested indent alignment 
  --top-menu-padding-left: 0;       // Default value for top menu item padding-left
  --top-menu-selected-text-color: #{ $menu-selected-text-color };
  --top-menu-selected-background-color: #{ $menu-selected-background-color };
  
  /* layout style */
  display: var(--flex-container-show);
  align-items: baseline;            // Align to baseline for top menu items
  flex-wrap: wrap;                  // needed for mobile screen where menu needs to be wrapped to next line
  width: auto;                      // flex container is block level element
  max-width: $max-width-nav;
  margin: 0 auto;
  height: var(--header-height);
  padding: calc((var(--header-height) - var(--nav-height)) / 2) 1em;
  border-bottom: 1px solid #e7e7e7;

  /* position style */
  position: sticky;
  top: 0;
  z-index: 999;

  color: $menu-text-color;
  background: $header-background-color;

  /* Make link anchor and label a flex container by default, least specific */ 
  a, label {
    display: var(--flex-container-show);              // flex container is a block level element itself
    align-items: baseline;      // Vertical alignment for elements inside a link
    color: unset;               // Inherit color from containing element (li)
    padding: 0 0.5em;
  }

  ul {                          // ul is block level element by default
    align-items: baseline;      // Vertical alignment for elements inside a link

    margin: 0;
    padding: 0;
    list-style: none;
    list-style-type: none;

    &.dropdown-menu {            // default width value is auto
      width: var(--dropdown-menu-width);
    }
  }

  li {                          // li is block level element by default
    position: relative;         // Serve as anchor point for sub-menu
    text-align: left;
    line-height: 1.5;           // Adding 50% font-height at each li top
    padding-left: var(--menu-padding-left);  // Space w/ background (vs margin) reserved for dropdown indicator and nested indent
    background-color: $menu-dropdown-background-color;
  }

  .separator {
    user-select: none;
    opacity: $navbar-separator-opacity;
    margin-right: 0.5em;
  }

  /* Hide elements not shown by default */
  .toggle,                      // Hide label unless it's mobile display
  .menu-icon,                   // Hide menu icon unless it's top menu in Pad size display
  .dropdown-menu,               // Hide sub-menu until parent dropdown clicked (mobile) or hover
  [id^=drop],                   // Always hide checkboxes, or use input as selector
  .separator:first-child,       // Show top menu separator except 1st one
  .dropdown-menu .separator {   // Always hide separators in dropdown-menu list
  	display: var(--hide);
  }

  /* .site-header flex items */
  .brand {                     // Flex container itself set default by a anchor
    margin-right: auto;         // Push siblings flex items "burger" and "menu" to far right 

    /* Its own Flex items */
    .logo {
      height: var(--nav-height);
      width: auto;
      border-radius: 0.2em;
      margin-right: 0.5em;
    }
 
    .site-title {
      font-size: var(--nav-height);
      line-height: 1;
      font-weight: bold;
      font-style: italic;
      font-family: $font-family-logo;
      color: $menu-text-color;
    }
  }

  // Default styling for hamburger button
  .hamburger {
    font-size: 120%;
  }

  // Menu list, default width:auto, expand to 100% for mobile display to wrap
  .menu {
    display: var(--flex-container-show);      // Making child li flex item, override default li width with auto
    align-items: baseline;
    justify-content: flex-end;    // Right aligned, use item margin to fix spaces between flex items since no width specified

    // menu text and background colors
    > li {
      padding-left: var(--top-menu-padding-left);    // Override default padding-left 1em

      > a, > label {
        border-radius: $menu-item-border;           // rounded top corners
        text-transform: uppercase;         // Upper case only top menu items

        color: $menu-text-color;
        background-color: $menu-background-color;
      }
  
      // Pointer event to change top menu highlight colors
      &:hover > a,
      &:hover > label,
      > [id^=drop]:checked ~ label {
        color: var(--top-menu-selected-text-color);
        background-color: var(--top-menu-selected-background-color);
      }

      li {
        > a, > label {
          color: $menu-dropdown-text-color;
          background: $menu-dropdown-background-color;
        }
        // Pointer event to change sub menu highlight colors
        &:hover > a,
        &:hover > label,
        > [id^=drop]:checked ~ label {
          color: $menu-dropdown-selected-text-color;
          background-color: $menu-dropdown-selected-background-color;
        }
      }
    }

    // All dropdown li defaults
    .dropdown {
      // Append left pointing triangle at beginning using pseudo class
      > a > span:before {
        content: "\25C0";
        font-size: 75%;
        position: absolute;          // using absolute to maintain text alignment
        top: 0.25em;
        right: calc(100% - 1em);    // prefix shown in li margin
      }
    }
    /* Make sub-menu ul position property less specific for easier @media override later */
    /* Absolute position parameters can be specific but won't matter once position property is override */
    .dropdown-menu {
      position: absolute;
      box-shadow: var(--sub-menu-box-shadow, $menu-dropdown-shadow);
      top: 0;
      right: 100%;
    }

    // Override for top menu dropdown li and dropdown menu ul
    > .dropdown {
      // Append down pointing triangle at end using pseudo class
      > a > span {
        &:after {
          content: "\25BC";
          font-size: 75%;
        }
        &:before {
          content: "";
        }
      }

      // Position top menu's dropdown-menu ul directly beneath, right aligned ro parent
      > .dropdown-menu {
        top: 100%;
        right: 0;
      }
    }
  }
}

/* ---- Responsive, desktop first ---- */
/* Large screen only styles */
@media screen and (min-width: $x-sm-break) {
  .site-header  .menu .dropdown {
    // Add dropdown hover event handler to show dropdown menu
    &:hover > ul {
      display: var(--ul-show);
    }
  }
}

/* Pad size screen, change menu to icon list */
@media screen and (max-width: $m-break) and (min-width: $x-sm-break) {
  .site-header > .menu > li > a {
    > .menu-icon {
      display: var(--flex-item-show);
    }
    > .menu-title {
      display: var(--hide);
    }
  }
}

/* Mobile size screen, change menu hamburger with pull-down drawer */
@media screen and (max-width: $x-sm-break) {
  :root {
    /* Shrink the header height a little bit  */
    --header-height: 2.5em;
  }

  .site-header {
    height: auto;         // to auto adjust height based on menu depth, pull/push sidebar

    /* Redefine custom properties for responsive override */
    --dropdown-menu-width: 100%;
    --sub-menu-box-shadow: none;
    --top-menu-padding-left: var(--menu-padding-left);
    --top-menu-selected-text-color: #{ $menu-dropdown-selected-text-color };
    --top-menu-selected-background-color: #{ $menu-dropdown-selected-background-color };
  
    /* Toggle element hide/show in drawer menu mode */
    .dropdown > a,        // Hide the dropdown link now, use labels instead for clickable drawer style dropdown sub-menu
    .menu .separator,     // Hide all separators in menu tree 
	  .menu {               // Hide the navigation menu till toggled by Hamburger button clicking, Specificity score is 10 for class
		  display: var(--hide);
    }
    .toggle {
      display: var(--flex-container-show);
    }
    
    .site-title {
      margin-top: 7px;
    }

    /* Prefix down pointing triangle to checkbox labels inside /menu */
    .menu .toggle {
      &:before {
        content: "\25BC";        // down pointing triangle
        font-size: 75%;
        position: absolute;          // using absolute to maintain text alignment
        top: 0.25em;
        right: calc(100% - 1em);    // prefix shown in li margin
      }
      &:after {
        content: "";
      }
    }

    /* Show Dropdown menu when clicked on sibling Label/checkbox, Specificity score is 21 = feature(10) + pseudo class(10) + element(1) */
    [id^=drop]:checked ~ ul {
      display: var(--ul-show);     // Convert top .menu to default ul display as well when "hamburger" label clicked
      width: 100%;
      position: static;            // override absolute settings for bigger screen

      transition: opacity .25s 0s, font-size .25s 0s;
    }
  }
}
